.footer {
  height: auto;
  width: 100%;
  padding: 1.3vw 0;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap: 2rem;
  background-color: $primary-text-color-blue;
  .header-tertiary {
    color: $bg-color-grey-dark;
    grid-column: 2/12;
  }
  .paragraph {
    color: $bg-color-grey-dark;
    font-weight: 200;
  }
  .agent-1 {
    grid-column: 3/6;
    grid-row: 2/3;
  }
  .agent-2 {
    grid-column: 3/6;
    grid-row: 3/4;
  }
  .address {
    font-family: "Merriweather", serif;
    grid-column: 6/8;
    grid-row: 2/3;
    font-style: normal;
  }
  .phone {
    &-1 {
      grid-column: 9/11;
      grid-row: 2/3;
    }
    &-2 {
      grid-column: 9/11;
      grid-row: 3/4;
    }
  }
  .open-time {
    grid-column: 6/8;
    grid-row: 3/4;
  }

  @media screen and (max-width: 1350px) {
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(5, 1fr);
    gap: 1rem;
    .header-tertiary {
      grid-column: 1/-1;
      justify-self: center;
    }
    .agent-1 {
      grid-column: 3/5;
      grid-row: 2/3;
    }
    .agent-2 {
      grid-column: 3/6;
      grid-row: 3/4;
    }
    .address {
      font-family: "Merriweather", serif;
      grid-column: 3/5;
      grid-row: 4/5;
      font-style: normal;
    }
    .phone {
      &-1 {
        grid-column: 6/9;
        grid-row: 2/3;
      }
      &-2 {
        grid-column: 6/9;
        grid-row: 3/4;
      }
    }
    .open-time {
      grid-column: 6/8;
      grid-row: 4/5;
    }
  }
  @media screen and (max-width: 650px) {
    grid-template-columns: 0.5fr repeat(3, 1fr) 0.5fr;
    grid-template-rows: repeat(8, 1fr);
    gap: 0.5rem;
    .agent-1 {
      grid-column: 3/-1;
      grid-row: 2/3;
    }
    .agent-2 {
      grid-column: 3/-1;
      grid-row: 4/5;
    }
    .address {
      font-family: "Merriweather", serif;
      grid-column: 3/-1;
      grid-row: 6/7;
      font-style: normal;
    }
    .phone {
      &-1 {
        grid-column: 3/-1;
        grid-row: 3/4;
      }
      &-2 {
        grid-column: 3/-1;
        grid-row: 5/6;
      }
    }
    .open-time {
      grid-column: 3/-1;
      grid-row: 7/8;
    }
  }
  @media screen and (max-width: 500px) {
    grid-template-rows: repeat(7, 1fr) 1.5fr;
  }
  .footer__icon {
    fill: $bg-color-grey-light;
    height: 4rem;
    width: 4rem;
    display: inline-block;
    transition: fill 0.2s ease;
    &:hover {
      fill: $lines-color-gold;
    }
    &.mail {
      &-first {
        grid-column: 2/3;
        grid-row: 2/3;
        justify-self: end;
      }
      &-second {
        grid-column: 2/3;
        grid-row: 3/4;
        justify-self: end;
      }
    }
    &.phone-first {
      grid-column: 8/9;
      grid-row: 2/3;
      justify-self: end;
    }
    &.phone-second {
      grid-column: 8/9;
      grid-row: 3/4;
      justify-self: end;
    }
    &.address {
      grid-column: 5/6;
      grid-row: 2/3;
      justify-self: end;
    }
    &.time {
      grid-column: 5/6;
      grid-row: 3/4;
      justify-self: end;
    }

    @media screen and (max-width: 1350px) {
      &.mail {
        &-first {
          grid-column: 2/3;
          grid-row: 2/3;
          justify-self: end;
        }
        &-second {
          grid-column: 2/3;
          grid-row: 3/4;
          justify-self: end;
        }
      }
      &.phone-first {
        grid-column: 5/6;
        grid-row: 2/3;
        justify-self: end;
      }
      &.phone-second {
        grid-column: 5/6;
        grid-row: 3/4;
        justify-self: end;
      }
      &.address {
        grid-column: 2/3;
        grid-row: 4/5;
        justify-self: end;
      }
      &.time {
        grid-column: 5/6;
        grid-row: 4/5;
        justify-self: end;
      }
    }

    @media screen and (max-width: 650px) {
      &.mail {
        &-first {
          grid-column: 2/3;
          grid-row: 2/3;
          justify-self: end;
        }
        &-second {
          grid-column: 2/3;
          grid-row: 4/5;
          justify-self: end;
        }
      }
      &.phone-first {
        grid-column: 2/3;
        grid-row: 3/4;
        justify-self: end;
      }
      &.phone-second {
        grid-column: 2/3;
        grid-row: 5/6;
        justify-self: end;
      }
      &.address {
        grid-column: 2/3;
        grid-row: 6/7;
        justify-self: end;
      }
      &.time {
        grid-column: 2/3;
        grid-row: 7/8;
        justify-self: end;
      }
    }
  }

  &__bottom {
    grid-column: 2/-2;
    grid-row: 4/5;
    border-top: 2px solid $lines-color-gold;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .paragraph {
      display: inline-block;
    }
    .svg-group {
      justify-self: end;
      display: inline-block;
      flex-direction: row;
      .icons {
        height: 5rem;
        width: 5rem;
        fill: $primary-text-color-white;
        border: 2px solid transparent;
        border-radius: 200%;
        transition: all 0.2s ease;
        &:hover {
          fill: $bg-color-grey-darker;
          border: 1px solid $lines-color-gold;
        }
      }
    }
    @media screen and (max-width: 1350px) {
      grid-column: 1/ -1;
      grid-row: 5/6;
      justify-content: space-evenly;
    }
    @media screen and (max-width: 650px) {
      grid-column: 1/ -1;
      grid-row: -2/-1;
      .svg-group {
        .icons {
          height: 4rem;
          width: 4rem;
        }
      }
    }
    @media screen and (max-width: 500px) {
      display: flex;
      flex-direction: column-reverse;
    }
  }
}
