.section {
  &__hire,
  &__advertisings,
  &__content,
  &__seo,
  &__socialmedia,
  &__immersive,
  &__partner,
  &__contact {
    transition: all 0.5s ease;
  }
}
.section__hero {
  height: 90vh;
  background: linear-gradient(
      to bottom,
      rgba($special-color-black, 1),
      rgba($special-color-black, 0.15)
    ),
    url("../resources/img/interior-deco-1.jpg");
  background-size: cover;
  background-position: 50% 50%;
  text-align: center;
  .header-primary,
  .header-secondary {
    text-shadow: 0 4px 1.1rem rgba($special-color-black, 1);
  }
  .header-primary {
    padding: 0 7.5vw;
    margin-bottom: 5vw;
  }
  .header-secondary {
    text-align: right;
    padding: 0 15vw;
  }
  @media screen and (max-width: 800px) {
    .header-secondary {
      text-align: center;
    }
  }
}

.section__hire {
  height: 30vw;
  background-color: $card-colors;
  text-align: center;
  padding: 2rem;

  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  margin-top: 4rem;
  background: linear-gradient(
      180deg,
      #ffffff 0%,
      rgba(255, 255, 255, 0.3) 44.79%,
      rgba(255, 255, 255, 0.3) 63.54%,
      #ffffff 100%
    ),
    url("../resources/img/agent.jpg");
  background-size: cover;
  background-position: center;
  .header-tertiary {
    margin-bottom: $margin-bottom-small;
    width: 90vw;
  }
  .paragraph {
    text-align: left;
    width: 72vw;
  }
  @media screen and (max-width: 876px) {
    height: auto;
    .paragraph {
      font-weight: bold;
    }
  }
}

.section__advertisings {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding: 2.6vw 5.2vw;
  @media screen and (max-width: 876px) {
    flex-direction: column;
    padding: 0;
  }
  &-textbox {
    background-color: $primary-text-color-blue-light;
    width: 50vw;
    height: 24.4vw;
    padding: 6rem;
    @media screen and (max-width: 1660px) {
      padding: 3rem;
    }
    @media screen and (max-width: 1024px) {
      height: 30vw;
      padding: 4rem;
    }
    @media screen and (max-width: 1000px) {
      padding: 2rem;
    }
    @media screen and (max-width: 876px) {
      width: 100%;
      height: auto;
    }

    @media screen and (max-width: 420px) {
      .header-tertiary {
        text-align: center;
      }
    }

    .header-tertiary {
      margin-bottom: 5rem;
      @media screen and (max-width: 1690px) {
        margin-bottom: 3rem;
      }
      @media screen and (max-width: 1300px) {
        margin-bottom: 1rem;
      }
    }
    .paragraph {
      text-align: center;
    }
    .header-tertiary,
    .paragraph {
      color: $primary-text-color-white;
      font-weight: 200;
    }
  }
  &-imgbox {
    position: relative;

    width: 35.4vw;
    height: 25vw;
    @media screen and (max-width: 1024px) {
      height: 30vw;
    }
    @media screen and (max-width: 876px) {
      left: 50%;
      transform: translateX(-50%);
      height: 75vw;
      width: 100%;
    }
  }
  &-img {
    position: absolute;
    box-shadow: 0 4px 1rem 0.4rem rgba($special-color-black, 0.25);
    &.first {
      width: 32vw;
      height: 21.4vw;
    }
    &.second {
      width: 20vw;
      height: 13.3vw;
      right: 0;
      top: 40%;
    }
    &.third {
      width: 10.8vw;
      height: 8.75vw;
      top: 62.5%;
      right: 40%;
    }
    @media screen and (max-width: 876px) {
      &.first {
        width: 90%;
        height: 90%;
      }
      &.second {
        width: 57%;
        height: 55%;
        right: 0;
        top: 40%;
      }
      &.third {
        width: 40%;
        height: 37%;
        top: 62.5%;
        right: 40%;
      }
    }
  }
}

.section__content {
  width: 100%;
  display: flex;
  flex-direction: row;
  padding: 0 5.2vw;
  background-color: $bg-color-grey-light;
  @media screen and (max-width: 910px) {
    flex-direction: column;
    justify-content: center;
    height: 45vw;
    padding: 2.5vw;
  }
  @media screen and (max-width: 731px) {
    height: auto;
    padding: 5.6vw;
  }
  &-textbox {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
    .paragraph {
      width: 54vw;
    }
    @media screen and (max-width: 932px) {
      .paragraph {
        width: 57vw;
      }
    }
    @media screen and (max-width: 824px) {
      .paragraph {
        width: 65vw;
      }
    }
    @media screen and (max-width: 823px) {
      .paragraph {
        width: 70vw;
      }
    }
    @media screen and (max-width: 770px) {
      .paragraph {
        width: 80vw;
      }
    }
  }
  &-img {
    height: 37vw;
    margin-top: auto;
    @media screen and (max-width: 910px) {
      display: none;
    }
  }
}

.section__seo {
  background-color: $bg-color-grey-dark;
  display: flex;
  justify-content: space-evenly;
  padding: 2.6vw 5.2vw;
  .header-tertiary {
    text-align: center;
    margin-bottom: 2vw;
  }
  .paragraph {
    width: 46.8vw;
  }
  &-img {
    width: 35vw;
    box-shadow: 0 4px 1rem 0.4rem rgba($special-color-black, 0.25);
  }
  @media screen and (max-width: 876px) {
    flex-direction: column;
    justify-content: center;
    align-content: center;
    text-align: center;
    .paragraph {
      width: 100%;
      margin-bottom: 2vw;
    }
    &-imgbox {
      max-height: 400px;
      overflow: hidden;
    }
    &-img {
      width: 100vw;
    }
  }
  @media screen and (max-width: 876px) {
    padding: 3vw 0 0;
    .paragraph {
      padding: 1vw 5vw;
    }
  }
}

.section__socialmedia {
  position: relative;
  height: 34vw;
  overflow: hidden;
  .bg-video {
    position: absolute;
    top: -20rem;
    z-index: -100;
    height: 100%;
    &__content {
      width: 100%;
    }
  }
  &-textbox {
    padding: 2.6vw 5.2vw;
    height: 100%;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
  }
  .paragraph {
    font-weight: 400;
  }
  .header-secondary,
  .paragraph {
    color: $primary-text-color-white;
  }
  .icons {
    width: 4.2vw;
    height: 4.2vw;
    fill: rgba($primary-text-color-white, 0.3);
  }
  .title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 3.2vw;
    margin-bottom: 3.2vw;
  }
  @media screen and (max-width: 876px) {
    height: auto;

    .bg-video {
      left: 50%;
      transform: translateX(-50%);
      &__content {
        height: 200%;
        width: auto;
      }
    }
    .title {
      display: flex;
      flex-direction: column;
      height: auto;
      .header-secondary {
        margin-bottom: 2vw;
        text-align: center;
      }
      .icons {
        height: 3rem;
        width: 3rem;
      }
    }
    .paragraph {
      margin-bottom: 2.6vw;
    }
  }
}

.section__immersive {
  padding: 2.6vw 5.2vw;
  display: flex;
  justify-content: space-evenly;
  height: auto;

  @media screen and (max-width: 990px) {
    flex-direction: column-reverse;
    margin-bottom: 0;
  }
  @media screen and (max-width: 800px) {
    padding: 0;
  }
  &-imgbox {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 1vw;
    height: auto;
    width: 42.7vw;
    overflow-x: hidden;

    @media screen and (max-width: 990px) {
      width: 100%;
    }
  }
  &-img {
    &.first {
      grid-column: 1/4;
      grid-row: 1/3;
      width: 100%;
      height: 100%;
    }
    &.second {
      grid-column: 4/7;
      grid-row: 1/2;
      width: 110%;
      height: 100%;
    }
    &.third {
      grid-column: 4/7;
      grid-row: 2/3;
      width: 100%;
      height: 100%;
    }
  }

  &-textbox {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    background-color: $primary-text-color-blue-light;
    width: 42.8vw;
    height: auto;
    text-align: center;
    padding: 1vw;

    @media screen and (max-width: 990px) {
      width: 100%;
      padding: 2vw 4vw;
    }
  }
  .paragraph {
    font-weight: 300;
    @media screen and (max-width: 1690px) {
      margin-top: 1rem;
    }
    margin-top: 5rem;
  }
  .header-tertiary,
  .paragraph {
    color: $primary-text-color-white;
  }
}

.section__partner {
  padding: 2.6vw 6.5vw;
  background: $bg-color-grey-light;
  display: flex;
  flex-direction: column;
  .article {
    display: flex;
    justify-content: space-evenly;
  }
  .header-tertiary {
    margin-bottom: 2rem;
  }
  .paragraph {
    width: 55vw;
  }
  &-img {
    box-shadow: 0 4px 1rem 0.4rem rgba($special-color-black, 0.25);
    width: 28vw;
    height: 17vw;
    align-self: center;
  }

  @media screen and (max-width: 876px) {
    padding: 0;
    flex-direction: column;
    .header-tertiary {
      padding: 2.6vw 2.6vw 0;
      margin-bottom: 0;
      text-align: center;
    }
    .paragraph {
      width: auto;
      padding: 2.6vw 6.5vw;
    }
    .article {
      flex-direction: column;
    }
    &-img {
      width: 100%;
      height: auto;
    }
  }
}
.section__contact {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.55), rgba(0, 0, 0, 0.55)),
    url("../resources/img/property.jpg");
  background-position: 50% 5%;
  background-size: cover;
  height: auto;
  padding: 5vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .header-secondary {
    margin-bottom: 5rem;
  }
  .paragraph {
    text-align: center;
    color: white;
    margin-bottom: 5vw;
  }
  @media screen and (max-width: 876px) {
    height: auto;
    padding: 10vw;
  }
}
