* {
  padding: 0;
  margin: 0;
}

.header-primary {
  font-size: 3.2rem;
  font-weight: normal;
  color: $primary-text-color-white;
}
.header-secondary {
  font-size: 2rem;
  color: $primary-text-color-white;
  line-height: 3rem;
  font-weight: 400;
}

.header-tertiary {
  font-size: 1.6rem;
  color: $primary-text-color-blue;
}
.paragraph {
  font-size: 1.2rem;
  line-height: 1.3rem;
  color: $primary-text-color-blue;
}
.white {
  color: $primary-text-color-white;
}
.btn {
  font-family: "Calistoga", cursive;
  font-size: 2.4rem;
  color: white;
}
.links {
  color: $bg-color-grey-dark;
  font-size: 1.8rem;
  transition: color 0.2s ease;
  &:hover {
    color: $bg-color-grey-darker;
  }
}

////////////////////////////
// MEDIA QUERIES

@media screen and (max-width: 1137px) {
  .header-primary {
    font-size: 5rem;
    color: $primary-text-color-white;
  }
  .header-secondary {
    font-size: 3.2rem;
    color: $primary-text-color-white;
    line-height: 4rem;
  }
  .header-tertiary {
    font-size: 2.4rem;
    font-weight: 500;
  }
  .paragraph {
    font-size: 1.2rem;
    line-height: 2.2rem;
    font-weight: 400;
  }
  .btn {
    font-size: 1.6rem;
  }
  .links {
    font-size: 1.2rem;
  }
  .nav-link {
    font-size: 1.7rem;
  }
}
@media screen and (min-width: 1138px) {
  .header-primary {
    font-size: 7rem;
    color: $primary-text-color-white;
  }
  .header-secondary {
    font-size: 4.2rem;
    color: $primary-text-color-white;
    line-height: 5rem;
  }
  .header-tertiary {
    font-size: 2.8rem;
    font-weight: 500;
  }
  .paragraph {
    font-size: 1.3rem;
    line-height: 3rem;
    font-weight: 400;
  }
  .btn {
    font-size: 1.7rem;
  }
  .links {
    font-size: 1.3rem;
  }
  .nav-link {
    font-size: 2rem;
  }
  .nav-link {
    font-size: 1.6rem;
  }
}
@media screen and (min-width: 1300px) {
  .header-primary {
    font-size: 8rem;
    color: $primary-text-color-white;
  }
  .header-secondary {
    font-size: 4.8rem;
    color: $primary-text-color-white;
    line-height: 5rem;
  }
  .header-tertiary {
    font-size: 3.2rem;
    font-weight: 500;
  }
  .paragraph {
    font-size: 1.4rem;
    line-height: 3rem;
    font-weight: 400;
  }
  .btn {
    font-size: 1.7rem;
  }
  .links {
    font-size: 1.3rem;
  }
  .nav-link {
    font-size: 1.6rem;
  }
}
@media screen and (min-width: 1600px) {
  .header-primary {
    font-size: 9rem;
    color: $primary-text-color-white;
  }
  .header-secondary {
    font-size: 5.4rem;
    color: $primary-text-color-white;
    line-height: 6rem;
  }
  .header-tertiary {
    font-size: 3.6rem;
    font-weight: 500;
  }
  .paragraph {
    font-size: 1.6rem;
    line-height: 4rem;
    font-weight: 400;
  }
  .btn {
    font-size: 1.9rem;
  }
  .links {
    font-size: 1.5rem;
  }
  .nav-link {
    font-size: 1.8rem;
  }
}
@media screen and (min-width: 1735px) {
  .header-primary {
    font-size: 10rem;
    color: $primary-text-color-white;
  }
  .header-secondary {
    font-size: 6rem;
    color: $primary-text-color-white;
    line-height: 7rem;
  }
  .header-tertiary {
    font-size: 4rem;
    font-weight: 500;
  }
  .paragraph {
    font-size: 1.8rem;
    line-height: 4rem;
    font-weight: 400;
  }
  .btn {
    font-size: 2.2rem;
  }
  .links {
    font-size: 1.8rem;
  }
  .nav-link {
    font-size: 2rem;
  }
}
