.header {
  width: 100%;
}
.nav {
  width: 100%;
  height: auto;
  display: flex;
  padding: 2vw;
  z-index: 100;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  transition: all 0.6s ease;
  .ham-icon {
    display: none;
    text-align: initial;
  }
  .nav__box-logo {
    transition: all 1s ease;
    display: inline-block;
  }
  .list {
    width: auto;
    display: flex;
    align-items: center;
    position: relative;
    justify-content: space-between;

    &__item {
      color: $primary-text-color-white;
      transition: all 1s ease;
      padding: 2rem 1rem;

      &-link {
        position: relative;
        text-transform: uppercase;
        text-decoration: none;
        color: $primary-text-color-white;
        &:after {
          content: "";
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          width: 100%;
          height: 3px;
          transform: scaleX(0);
          background-color: $lines-color-gold;
          transition: transform 0.3s;
        }
        &:hover {
          color: $bg-color-grey-darker;
          &:after {
            transform: scaleX(1);
          }
        }
      }
    }
  }
  @media screen and (max-width: 800px) {
    justify-content: flex-start;
    padding: 3vw;
    .ham-icon {
      display: block;
    }
    .list {
      display: none;
      flex-direction: column;
      position: absolute;
      transform: translateY(-100vw);
    }
    .nav__box-logo {
      padding: 1vw 8vw;
    }
  }
}
