.btn {
  background: transparent;
  text-transform: uppercase;
  border: 1px solid $bg-color-grey-dark;
  padding: 2rem 1rem;
  transition: all 0.2s ease;
  &:hover {
    background: rgba($bg-color-grey-light, 0.3);
    cursor: pointer;
  }
  &:active {
    transform: scale(0.97) translateY(-2px);
  }
  &:focus {
    outline: none;
  }
}

$width: 4.5rem;
$height: 3px;
$separation: 10px;

.ham-icon {
  width: 6rem;
  height: 4rem;
  position: absolute;
  right: 5vw;
  z-index: 300;
  .ham-line {
    transition: all 0.3s ease;
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: $width;
    height: $height;
    background: $bg-color-grey-dark;
    box-shadow: 0 0 0.3rem 0.1rem rgba($lines-color-gold, 0.5);
    border-radius: 30%;
    &::before {
      transition: all 0.3s ease;
      content: "";
      width: $width;
      height: $height;
      background: $bg-color-grey-dark;
      position: absolute;
      bottom: $separation;
      box-shadow: 0 0 0.3rem 0.1rem rgba($lines-color-gold, 0.5);
      border-radius: 30%;
    }
    &::after {
      transition: all 0.3s ease;
      content: "";
      width: $width;
      height: $height;
      background: $bg-color-grey-dark;
      position: absolute;
      top: $separation;
      box-shadow: 0 0 0.3rem 0.1rem rgba($lines-color-gold, 0.5);
      border-radius: 30%;
    }
  }
  &.active .ham-line {
    background-color: transparent;
    box-shadow: none;
    &::before {
      opacity: 1;
      transform: translateY(1rem) rotate(225deg);
    }
    &::after {
      transform: translateY(-0.9rem) rotate(-225deg);
    }
  }
}

.hidde {
  display: none;
}
.sticky {
  position: fixed;
  background: linear-gradient(
    to bottom,
    rgba($special-color-black, 1),
    rgba($special-color-black, 0.9)
  );
  .nav__box-logo {
    height: 5rem;
    transition: all 1s ease;
    display: inline-block;
  }
}

.nav.sticky {
  padding: 0;
}
.section--hidden {
  transform: translateY(7rem);
  opacity: 0;
}
.translate {
  transform: translateX(-100vw);
}
.show-nav {
  padding: 0;
  .list__item:nth-child(2) {
    top: 5rem;
  }
}
.nav .list.show-nav {
  display: flex;
  transition: all 1s ease;
  left: -5vw;
  transform-origin: top;
  transform: translateY(10rem);
  background: linear-gradient(
    to bottom,
    rgba($special-color-black, 1),
    rgba($special-color-black, 1)
  );
  width: 110%;
  z-index: 200;
}
